'use client';
import React, { useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from "recharts";
import { Separator } from "@/components/ui/separator";

export default function InvestmentDashboard() {
  const [rentabilidade, setRentabilidade] = useState(3.55);
  const [investInicial, setInvestInicial] = useState(20000);
  const [aporteMensal, setAporteMensal] = useState(1500);
  const [aporteSazonal, setAporteSazonal] = useState(8000);
  const [mesSazonal, setMesSazonal] = useState(4);
  const [vendasMensais, setVendasMensais] = useState(40);
  const [nMeses, setNMeses] = useState(12);
  const [n1, setN1] = useState(5);
  const [n2, setN2] = useState(10);
  const [n3, setN3] = useState(15);

  const licencaAfiliado = 277.9;

  const simulate = () => {
    let capital = investInicial;
    let capitalIndicado = investInicial;
    let simulacao = [];
    let totalLucro = 0;
    let totalReceitaLicenca = 0;
    let totalComissao = 0;

    for (let mes = 1; mes <= nMeses; mes++) {
      capital += aporteMensal;
      capitalIndicado += aporteMensal;
      if (mes === mesSazonal) {
        capital += aporteSazonal;
        capitalIndicado += aporteSazonal;
      }

      let lucro = capital * (rentabilidade / 100);
      let lucroIndicado = capitalIndicado * (rentabilidade / 100);
      capital += lucro;
      capitalIndicado += lucroIndicado;

      let receitaLicenca = vendasMensais * licencaAfiliado;

      let comissao =
        lucroIndicado *
        (n1 * 0.55 + n2 * 0.25 + n3 * 0.15);

      totalLucro += lucro;
      totalReceitaLicenca += receitaLicenca;
      totalComissao += comissao;

      simulacao.push({
        mes: `Mês ${mes}`,
        capital: parseFloat(capital.toFixed(2)),
        lucro: parseFloat(lucro.toFixed(2)),
        receitaLicenca: parseFloat(receitaLicenca.toFixed(2)),
        comissao: parseFloat(comissao.toFixed(2))
      });
    }

    return { data: simulacao, totalLucro, totalReceitaLicenca, totalComissao, capitalFinal: capital };
  };

  const { data, totalLucro, totalReceitaLicenca, totalComissao, capitalFinal } = simulate();

  return (
    <div className="p-6 space-y-6 bg-black text-white min-h-screen font-sans">
      <h1 className="text-3xl font-bold text-cyan-400">Projeção de Investimentos</h1>

      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
        <Card className="bg-gradient-to-br from-cyan-500/30 to-transparent border-cyan-400">
          <CardContent className="p-4">
            <p className="text-sm">Capital Final</p>
            <p className="text-xl font-semibold">R$ {capitalFinal.toLocaleString()}</p>
          </CardContent>
        </Card>
        <Card className="bg-gradient-to-br from-green-500/30 to-transparent border-green-400">
          <CardContent className="p-4">
            <p className="text-sm">Lucro Total</p>
            <p className="text-xl font-semibold">R$ {totalLucro.toLocaleString()}</p>
          </CardContent>
        </Card>
        <Card className="bg-gradient-to-br from-orange-500/30 to-transparent border-orange-400">
          <CardContent className="p-4">
            <p className="text-sm">Receita com Licenças</p>
            <p className="text-xl font-semibold">R$ {totalReceitaLicenca.toLocaleString()}</p>
          </CardContent>
        </Card>
        <Card className="bg-gradient-to-br from-red-500/30 to-transparent border-red-400">
          <CardContent className="p-4">
            <p className="text-sm">Comissões Recebidas</p>
            <p className="text-xl font-semibold">R$ {totalComissao.toLocaleString()}</p>
          </CardContent>
        </Card>
      </div>

      <Separator className="bg-cyan-400 my-4" />

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Input type="number" value={investInicial} onChange={(e) => setInvestInicial(+e.target.value)} placeholder="Investimento Inicial" />
        <Input type="number" value={aporteMensal} onChange={(e) => setAporteMensal(+e.target.value)} placeholder="Aporte Mensal" />
        <Input type="number" value={aporteSazonal} onChange={(e) => setAporteSazonal(+e.target.value)} placeholder="Aporte Sazonal" />
        <Input type="number" value={mesSazonal} onChange={(e) => setMesSazonal(+e.target.value)} placeholder="Mês do Aporte Sazonal" />
        <Input type="number" value={rentabilidade} onChange={(e) => setRentabilidade(+e.target.value)} placeholder="Rentabilidade (%)" />
        <Input type="number" value={vendasMensais} onChange={(e) => setVendasMensais(+e.target.value)} placeholder="Vendas Mensais" />
        <Input type="number" value={n1} onChange={(e) => setN1(+e.target.value)} placeholder="Indicações Nível 1" />
        <Input type="number" value={n2} onChange={(e) => setN2(+e.target.value)} placeholder="Indicações Nível 2" />
        <Input type="number" value={n3} onChange={(e) => setN3(+e.target.value)} placeholder="Indicações Nível 3" />
        <Input type="number" value={nMeses} onChange={(e) => setNMeses(+e.target.value)} placeholder="Meses de Simulação" />
      </div>

      <Card className="mt-4">
        <CardContent className="p-4">
          <ResponsiveContainer width="100%" height={400}>
            <LineChart data={data}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="mes" stroke="#ccc" />
              <YAxis stroke="#ccc" />
              <Tooltip />
              <Legend />
              <Line type="monotone" dataKey="capital" stroke="#00bcd4" name="Capital Acumulado" />
              <Line type="monotone" dataKey="lucro" stroke="#4caf50" name="Lucro Mensal" />
              <Line type="monotone" dataKey="receitaLicenca" stroke="#ff9800" name="Receita com Licenças" />
              <Line type="monotone" dataKey="comissao" stroke="#f44336" name="Comissões da Rede" />
            </LineChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </div>
  );
}
